.profile__title {
    font-family: 'Inter', Arial, Helvetica, sans-serif;
    font-style: normal;
    font-weight: 500;
    font-size: 27px;
    line-height: 33px;
    text-align: center;
    color: #FFFFFF;
    margin: 0;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    max-width: 198px;
}

@media screen and (min-width: 1280px) {
    .profile__title {
        font-size: 42px;
        line-height: 48px;
        max-width: 294px;
    }
}
